form {
    --bgColor: white;
    --inputColor: black;
    --outlineColor: dodgerblue;
    --beamColor: yellow;
    --spacer: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    transform: translate3d(0,0,0);
    padding: var(--spacer);
    
    > * + * {
      margin-top: var(--spacer);
    }
  }
  
  .form-item {
    > * + * {
      margin-top: 0.5rem;
    }
  }

  
  #password {
    background-color: white;
    border-radius: 10px;
  }

  button {
    border: none;
  }
  
  [id="submit"] {
    cursor: pointer;
    margin: calc(var(--spacer) * 2) 0 0 8px;
    padding: 1.5rem 1.25rem;
    color: black;
    background-color: black;
    box-shadow: 4px 4px 0 rgba(dodgerblue, 0.2);
    
    &:active {
      transform: translateY(1px);
    }
  }
  
  .input-wrapper {
    position: relative;
  }
  
  input {
    padding: 0.75rem 4rem 0.75rem 0.75rem;
    width: 100%;
    border: 2px solid transparent;
    border-radius: 0;
    background-color: transparent;
    box-shadow: 
      inset 0 0 0 2px black,
      inset 6px 6px 0 rgba(dodgerblue, 0.2),
      3px 3px 0 rgba(dodgerblue, 0.2);
    -webkit-appearance: none;
  
    &:focus {
      outline-offset: 1px;
    }
    
    .show-password & {
      box-shadow: inset 0 0 0 2px black;
      border: 2px dashed white;
      
      &:focus {
        outline: none;
        border-color: var(--beamColor);
      }
    }
  }
  
  [id="eyeball"] {
    --size: 1.5rem;
    
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    outline: none;
    position: absolute;
    top: 50%;
    right: 0.75rem;
    border: none;
    background-color: transparent;
    transform: translateY(-50%);
    
    &:active {
      transform: translateY(calc(-50% + 1px));
    }
  }
  
  .eye {
    width: var(--size);
    height: var(--size);
    border: 2px solid black;
    border-radius: calc(var(--size) / 1.5) 0;
    transform: rotate(45deg);
    
    &:before,
    &:after {
      content: '';  
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      border-radius: 100%;
    }
    
    &:before {
      width: 35%;
      height: 35%;
      background-color: black;
    }
    
    &:after {
      width: 65%;
      height: 65%;
      border: 2px solid black;
      border-radius: 100%;
    }
  }
  
  [id="beam"] {  
    position: absolute;
    top: 50%;
    right: 1.75rem;
    clip-path: polygon(100% 50%, 100% 50%, 0 0, 0 100%);
    width: 100vw;
    height: 25vw;
    z-index: 1;
    mix-blend-mode: multiply;
    transition: transform 200ms ease-out;
    transform-origin: 100% 50%;
    transform: translateY(-50%) rotate(var(--beamDegrees, 0));
    pointer-events: none;
    opacity: 50%;
    
    .show-password & {
      background: var(--beamColor);
    }
  }

  .loginBtn {
    padding: 10px 20px;
    font-size: 16px;
    background-color: green; 
    color: white; 
    border: 2px solid black;
    cursor: pointer;
    border-radius: 10px;
  }
  
  .loginBtn:hover {
    background-color: #0056b3;
  }